.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #333;
    color: #fff;
    padding: 10px 20px;
  }
  
  .logo h1 {
    margin: 0;
    font-size: 24px;
  }
  
  .actions button {
    background-color: #4caf50;
    color: #fff;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 16px;
  }
  
  .actions button:hover {
    background-color: #45a049;
  }
  
  .user-details {
    background-color: #f9f9f9;
    padding: 20px;
    margin-top: 10px;
    border-radius: 5px;
  }
  
  .user-details p {
    margin: 5px 0;
  }
  